import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import FormTambahBarang from "./Component/FormTambahBarang";
import axios from "axios";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import ReactTooltip from "react-tooltip";
import { reset } from "redux-form";
import {
  EditPindahBarang,
  getListPindahBarang,
} from "../actions/pindahBarangAction";
import { getUUID } from "../actions/fakturAction";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../actions/LogicAction";
import Axios from "axios";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import { Link } from "react-router-dom";
import { deleteIndex } from "../deleteLocalStorage";
import FormEditTambahBarang from "./Component/FormEditTambahBarang";

function formatterCurency(cell) {
  return <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>;
}

const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;

const maptoState = (state) => {
  return {
    listBarang: state.provinsi.pindahBarang,
    listEditPindahBarang: state.provinsi.listEditPindahBarang,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
  };
};

class PindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dari: [],
      kepada: [],
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
      tanggal: "",
      dariIsi: "",
      ke: "",
      noBon: "",
      kategori: "",
      kodeJenis: "",
      keterangan: "",
      edited: false,
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "kadarTransaksi",
          text: "Kat Harga",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar",
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "rp",
          text: "Ongkos (Rp)",
          formatter: formatterCurency,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() =>
                    deleteIndex(
                      "pindahBarang",
                      rowIndex,
                      this.props.dispatch,
                      getListPindahBarang()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }
  getSumData() {
    this.props.dispatch(getNetto("pindahBarang"));
    this.props.dispatch(getBruto("pindahBarang"));
    this.props.dispatch(getRp("pindahBarang"));
    this.props.dispatch(getPkg("pindahBarang"));
    this.props.dispatch(getQty("pindahBarang"));
  }
  setEdit(row) {
    this.props.dispatch(EditPindahBarang(row));
    this.setState({
      edited: true,
    });
  }
  getTabelData() {
    this.props.dispatch(getListPindahBarang());
  }
  componentDidMount() {
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          dari: res.data,
          kepada: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getListPindahBarang());
    this.props.dispatch(getUUID());
    this.getSumData();
    this.dataEditPindahBarang();
  }
  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
    });
    var barang1 = JSON.parse(localStorage.getItem("pindahBarang")) || [];
    var simpan = {
      p: 0,
      discGlobalPr: 0,
      discGlobalGr: 0,
      qty: 1,
      pkg: parseFloat(data.pkg),
      noBarcode: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi),
      gross: 0,
      k: 0,
      nettoDiscGlobal: 0,
      kadarCetak: parseFloat(data.kadar),
      nettoDiscGlobalExt: 0,
      noFaktur: this.props.noFaktur.toUpperCase(),
      netto: parseFloat(data.netto),
      bruto: parseFloat(data.bruto),
      kodeJenis: result_explode[0].toUpperCase(),
      rp: parseFloat(data.ongkos ? data.ongkos : 0),
      kodeKategori: "-",
      keterangan: data.keterangan.toUpperCase(),
    };
    barang1.push(simpan);
    localStorage.setItem("pindahBarang", JSON.stringify(barang1));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Transaksi Berhasil",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("FormTambahBarang")))
      .then(() => this.props.dispatch(getListPindahBarang()))
      .then(() => this.getSumData());
  }

  SubmitPindah() {
    if (this.state.keterangan === "") {
      warning("Mohon Isi Kategori");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon isi Nomor Bon");
      return false;
    } else if (this.state.dariIsi === "") {
      warning("Mohon isi Tujuan Pindah");
      return false;
    } else if (this.state.ke === "") {
      warning("Mohon isi Asal Pindah");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi tanggal");
      return false;
    } else if (localStorage.getItem("pindahBarang") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var isi = {
      kategoriTransaksi: this.state.kategori.toUpperCase(),
      kodeLokasiTujuan: this.state.ke.toUpperCase(),
      noBon: this.state.noBon.toUpperCase(),
      noFaktur: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal.toUpperCase(),
      kodeLokasiAsal: this.state.dariIsi.toUpperCase(),
      keterangan: this.state.keterangan.toUpperCase(),
      barangDetails: JSON.parse(localStorage.getItem("pindahBarang")),
      footerItem: {
        totalNetto: this.props.sumNetto,
        totalBruto: this.props.sumBruto,
        totalQty: this.props.sumQty,
        totalpkg: this.props.sumPkg,
        totalGross: this.props.sumBruto + this.props.sumPkg,
        totalK: 0,
        totalP: 0,
        totalNettoExt: 0,
        totalRp: this.props.sumRp,
      },
      footerDocument: {
        inputBy: this.props.username.toUpperCase(), //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(isi));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("pindahBarang"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
        ticket.pkg,
        ticket.gross,
        parseFloat(ticket.rp).toLocaleString("id-ID"),
        ticket.qty,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "Grand Total",
      table.map((list) => list.bruto).reduce((a, b) => a + b, 0),
      "",
      table.map((list) => list.netto).reduce((a, b) => a + b, 0),
      table.map((list) => list.pkg).reduce((a, b) => a + b, 0),
      table.map((list) => list.gross).reduce((a, b) => a + b, 0),
      table.map((list) => list.rp).reduce((a, b) => a + b, 0),
      table.map((list) => list.qty).reduce((a, b) => a + b, 0),
    ];
    tableRows.push(footer);
    var columTable = [
      "No",
      "Kode Jenis",
      "Bruto (Gr)",
      "Harga (%)",
      "Netto (Gr)",
      "Pkg (Gr)",
      "Gross (Gr)",
      "Ongkos (Rp)",
      "Qty",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "item/dc/pindahbarang", isi)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              this.state.tanggal.toUpperCase(),
              "Lokasi",
              this.state.dariIsi + " - " + this.state.ke,
              "Nomor Bon",
              this.state.noBon.toUpperCase(),
              "",
              "",
              "ADMIN",
              this.state.tanggal.toUpperCase(),
              "",
              columTable,
              "RETURN ADMIN",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("pindahBarang"))
          .then(() =>
            this.setState({
              dariIsi: "",
              ke: "",
              noBon: "",
              kategori: "",
              kodeJenis: "",
            }).then(() => this.forceUpdate())
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => console.log(err))
      );
    localStorage.removeItem("pindahBarang");
    this.props.dispatch(getListPindahBarang());
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  resetField() {
    localStorage.removeItem("editPindahBarang");
    localStorage.removeItem("pindahBarang");
    this.props.dispatch(getUUID());
    this.props.dispatch(getListPindahBarang());
    this.setState({
      tanggal: "",
      dariIsi: "",
      ke: "",
      noBon: "",
      kategori: "",
      kodeJenis: "",
      keterangan: "",
    });
  }
  dataEditPindahBarang() {
    var data = JSON.parse(localStorage.getItem("editPindahBarang")) || [];
    this.setState({
      dariIsi: data.kodeLokasiAsal,
      ke: data.kodeLokasiTujuan,
      tanggal: data.tanggal,
      keterangan: data.keterangan,
      noBon: data.noBon,
    });
    this.props.dispatch(getListPindahBarang());
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-8">
                    <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                      Pindah Barang
                    </h5>
                  </div>
                  <div className="col-lg-3">
                    <div className="text-right">
                      <Link to="/ayugold.com/EditForm/pindahBarang">
                        <button
                          className="btn btn-warning"
                          // data-toggle="modal"
                          // data-target="#editPindahBarangModal"
                          // data-backdrop="static"
                          // data-keyboard="false"
                        >
                          Edit Pindah Barang
                        </button>
                      </Link>
                    </div>
                  </div>
                  <div className="col-lg-1">
                    <div className="text-right">
                      <button
                        className="btn btn-warning"
                        onClick={() => this.resetField()}
                      >
                        Reset
                      </button>
                    </div>
                  </div>
                  <div className="col-md-3 form-group">
                    <label>Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tannggal"
                      className="form-control"
                      onChange={this.onChange("tanggal")}
                      defaultValue={this.state.tanggal}
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Dari</label>
                    <select
                      id="dari"
                      name="dari"
                      className="form-control"
                      onChange={this.onChange("dariIsi")}
                      value={this.state.dariIsi}
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        SILAHKAN PILIH
                      </option>
                      {this.state.dari.map((dari) => (
                        <option
                          value={dari.kodeSupplier}
                          key={dari.kodeSupplier}
                        >
                          {dari.namaSupplier}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-1 text-center">
                    <label htmlFor="" className="text-white">
                      .
                    </label>
                    <h3>
                      <i className="fa fa-exchange-alt"></i>
                    </h3>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Ke</label>
                    <select
                      id="dari"
                      name="dari"
                      className="form-control"
                      onChange={this.onChange("ke")}
                      value={this.state.ke}
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        SILAHKAN PILIH
                      </option>
                      {this.state.dari.map((dari) => (
                        <option
                          value={dari.kodeSupplier}
                          key={dari.kodeSupplier}
                        >
                          {dari.namaSupplier}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Kategori</label>
                    <select
                      id="kategori"
                      name="kategori"
                      className="form-control"
                      onChange={this.onChange("keterangan")}
                      value={this.state.keterangan}
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        SILAHKAN PILIH
                      </option>
                      {this.state.listKategori.map((list) => (
                        <option value={list.nama} key={list.nama}>
                          {list.nama}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Bon Pindah</label>
                    <input
                      type="text"
                      id="noBon"
                      name="noBon"
                      className="form-control"
                      onChange={this.onChange("noBon")}
                      value={this.state.noBon}
                    />
                  </div>
                  <div className="col-lg-4"></div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-left">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        onClick={() => this.SubmitPindah()}
                      >
                        SIMPAN
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-center">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        disabled
                      >
                        BARCODE (+)
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                        disabled
                      >
                        BARCODE (-)
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-target="#primary-header-modal"
                      >
                        TAMBAH BARANG
                      </button>
                    </div>
                  </div>
                  <div className="col-md-12 col-lg-12">
                    {this.props.listBarang ? (
                      <BootstrapTable
                        keyField="KodeBarang"
                        data={this.props.listBarang}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                      />
                    ) : (
                      <BootstrapTable
                        keyField="KodeBarang"
                        data={[]}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                        noDataIndication="Tidak Ada Data"
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Pindah Barang
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormTambahBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                  listBarang={this.props.listBarang}
                  setEdit={this.state.setEdit}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Pindah Barang
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormEditTambahBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                  listBarang={this.props.listBarang}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(PindahBarang);
